@charset "utf-8";
$text_color : #444444;
$border_color: #e3e3e3;
$green: #27ae60;
$gray: #666666;
$light_gray: #999999;
$blue: #19a2d8;
$light_blue: #4dbce9;
$link_color: #108dbf;
$very_light_blue: #eaf3f7;
$another_light_blue :#f3f8fa;
$border-color-dark: #d7d7d7;

@mixin widthToPricentByPixelOn1200($pixel){
    width: $pixel * 100% / 1200;
}
@mixin transition($transition...) {
    -moz-transition:    $transition;
    -o-transition:      $transition;
    -webkit-transition: $transition;
    transition:         $transition;
}
body{
    font-family: 'Roboto', sans-serif;
    color: $text_color;
    font-size: 14px;
    overflow-x: hidden;
}
a{
    color: $link_color;
    &:hover{
        color: $link_color;
    }
}
@media (min-width: 1200px) {
    .container {
        width: 1170px;
    }
}
@media (min-width: 1230px) {
    .container {
        width: 1200px;
    }
}
.js-link{
    position: relative;
    &:after{
        content: '';
        display: block;
        left: 0;
        right: 0;
        bottom: 0;
        border-top: 1px dotted $text_color;
    }
}
header.line{
    border-bottom: 1px solid  $border_color;
    min-height: 49px;
    line-height: 45px;
    font-size: 14px;
    padding-top: 3px;
    a{
        color: $text_color;
    }
    .js-link{
        &:after{
            margin-top: -15px;
        }
    }
    .icon{
        margin-right: 9px;
        &.geo{
            margin-top: 15px;
        }
        &.time{
            margin-top: 15px;
        }
        &.email{
            margin-top: 17px;
        }
        &.phone{
            margin-top: 16px;
            @media (max-width: 1199px) {
                margin-top: 4px;
            }
        }
    }
    .row{
        @media (min-width: 992px) {
            >.col1{
                @include widthToPricentByPixelOn1200(236);
            }
            >.col2{
                @include widthToPricentByPixelOn1200(289);
            }
            >.col3{
                @include widthToPricentByPixelOn1200(304);
            }
            >.col4{
                @include widthToPricentByPixelOn1200(371);
            }
        }
    }
    .phones{
      display: inline-block;
      float: right;
        @media (max-width: 1199px) {
            // line-height: 20px;
        }
        @media (max-width: 992px) {
          float: none;
        }
        a {
          float: right;
          @media (max-width: 992px) {
            float: none;
            display: inline-block;
          }
        }
        .phone{
            margin-left: 0px;
            font-size: 18px;
            font-weight: bold;
            &:first-child{
                margin-left: 16px;
                @media (max-width: 992px) {
                  margin-left: 0;
                }
            }
        }
        .icon {
          display: inline-block;
        }
    }
}
header.header{
    background: #e1ebee url(../bitrix/templates/s2018//images/bgh.png) 0 0 repeat;
    min-height: 157px;
    border-bottom: 1px solid  $border_color;
    padding-top: 21px;
    .logo{
        display: block;
    }
    .row{
        @media (min-width: 992px) {
            >.col1{
                @include widthToPricentByPixelOn1200(236);
            }
            >.col2{
                @include widthToPricentByPixelOn1200(765);
            }
            >.col3{
                @include widthToPricentByPixelOn1200(199);
            }
        }
        &.menu-items{
            @media (min-width: 992px) {
                >.col1{
                    @include widthToPricentByPixelOn1200(251);
                }
                >.col2{
                    @include widthToPricentByPixelOn1200(621);
                    background-color: $blue;
                }
                >.col3{
                    @include widthToPricentByPixelOn1200(149);
                }
                >.col4{
                    @include widthToPricentByPixelOn1200(179);
                }
            }
        }
    }
    .form-group-search{
        margin-top: 8px;
        position: relative;
        @media (min-width: 992px) {
            margin-left: -1px;
            margin-bottom: 0;
        }
        .submit_button{
            position: absolute;
            top: 0;
            right: 0;
        }
        .form-control{
            padding-right: 100px;
        }
        .btn.btn-default{
            background: none;
            background-color: $very_light_blue;
            border-radius:  0 2px 2px 0;
            border-color: #d7d7d7;
            padding: 6px 29px;
        }
    }
    .btn-collMeWrap{
        margin-top: 8px;
    }
    .menu-items{
        margin-top: 22px;
    }
    .menu-item{
        display: block;
        line-height: 30px;
        padding: 17px 0 14px 0;
        background-color: $blue;
        font-size: 14px;
        color: #ffffff;
        font-weight: bold;
        margin-bottom: -1px;
        text-transform: uppercase;
        text-align: center;
        position: relative;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
        outline: none;
        &:active:focus,
            &:active,
            &:focus,
            &:hover{
            text-decoration: none;
            background-color: #42b5e3;
            outline: none;
        }
        &.catalog-menu-item{
            background-color: $green;
            padding-left: 46px;
            padding-right: 36px;
            @media  (max-width: 1199px) and (min-width: 992px) {
                padding-left: 27px;
                padding-right: 23px;
            }
            .icon{
                position: absolute;
                &.catalog{
                    top: 26px;
                    left: 29px;
                    @media  (max-width: 1199px) and (min-width: 992px) {
                        left: 8px;
                    }
                }
                &.down{
                    top: 28px;
                    right: 27px;
                    @media  (max-width: 1199px) and (min-width: 992px) {
                        right: 8px;
                    }
                }
            }
        }
        &.special-menu-item{
            background-color: #108dbf;
            line-height: 14px;
            text-align: left;
            padding-left: 55px;
            padding-right: 5px;
            @media  (max-width: 1199px) and (min-width: 992px) {
                padding-left: 34px;
            }
            &.special-menu-item-cart{
                padding-left: 72px;
                @media  (max-width: 1199px) and (min-width: 992px) {
                    padding-left: 38px;
                }
                &:after{
                    content: '';
                    display: block;
                    position: absolute;
                    top: 0;
                    bottom: 0;
                    left: 0;
                    background-color: $blue;
                    width: 1px;
                }
            }
            .icon{
                position: absolute;
                &.personal{
                    top: 17px;
                    left: 23px;
                    @media  (max-width: 1199px) and (min-width: 992px) {
                        left: 6px;
                    }
                }
                &.cart{
                    top: 22px;
                    left: 21px;
                    @media  (max-width: 1199px) and (min-width: 992px) {
                        left: 6px;
                    }
                }
            }
            .small{
                display: block;
                text-transform: none;
                font-size: 12px;
                color: #b0e5fa;
                line-height: 16px;
                font-weight: normal;
                white-space: nowrap;
                text-overflow: ellipsis;
                overflow: hidden;
            }
            .value{
                position: absolute;
                top: 12px;
                left: 38px;
                min-height: 24px;
                width: 24px;
                background-color: $green;
                line-height: 24px;
                text-align: center;
                color: #ffffff;
                font-size: 12px;
                font-weight: normal;
                border-radius: 50%;
                @media  (max-width: 1199px) and (min-width: 992px) {
                    left: 14px;
                    top: 6px;
                }

            }
        }
    }
}
.form-control{
    border-radius: 2px;
    font-size: 13px;
    line-height: 36px;
    height: 38px;
    padding-left: 22px;
    border-color: #d7d7d7;

}
.btn{
    font-size: 13px;
    line-height: 24px;
}
.btn-primary{
    background: none;
    background-color: $blue;
    border-color: $blue;
    -webkit-box-shadow: none;
    box-shadow: none;
    &:active:focus,
        &.active,
        &:active,
        &:hover,
        &:focus{
        background-color: $blue;
        border-color: $blue;
    }
}
.btn-default{
    background: none;
    background-color: #d6e4ec;
    border-color: #d6e4ec;
    color: $text_color;
    -webkit-box-shadow: none;
    box-shadow: none;
    text-shadow: none;
    &:active:focus,
        &.active,
        &:active,
        &:hover,
        &:focus{
        background-color: #d6e4ec;
        border-color: #d6e4ec;
    }
}
.btn-white{
    background: none;
    background-color: #fffff;
    border-color: $border_color;
    color: $text_color;
    -webkit-box-shadow: none;
    box-shadow: none;
    text-shadow: none;
    &:active:focus,
        &.active,
        &:active,
        &:hover,
        &:focus{
        background-color: #fffff;
        border-color: $border_color;
    }
}
.btn-white-green{
    background: none;
    background-color: #fffff;
    border: 2px solid $green;
    line-height: 22px;
    color: $text_color;
    -webkit-box-shadow: none;
    box-shadow: none;
    text-shadow: none;
    &:active:focus,
        &.active,
        &:active,
        &:hover,
        &:focus{
        background-color: #fffff;
        border-color: $green;
    }
}
.btn-success{
    background: none;
    background-color: $green;
    border-color: $green;
    color: #ffffff;
    -webkit-box-shadow: none;
    box-shadow: none;
    text-shadow: none;
    &:active:focus,
        &.active,
        &:active,
        &:hover,
        &:focus{
        color: #ffffff;
        background-color: $green;
        border-color: $green;
    }
}
.btn-gray{
    background: none;
    background-color: #91a2ac;
    border-color: #91a2ac;
    color: #ffffff;
    -webkit-box-shadow: none;
    box-shadow: none;
    text-shadow: none;
    &:active:focus,
        &.active,
        &:active,
        &:hover,
        &:focus{
        background-color: #91a2ac;
        border-color: #91a2ac;
        color: #ffffff;
    }
}
.btn-sort{
    background: none;
    background-color: #ffffff;
    border-color: $border_color;
    color: $text_color;
    -webkit-box-shadow: none;
    box-shadow: none;
    text-shadow: none;
    &:active:focus,
        &.active,
        &:active,
        &:hover,
        &:focus{
        background-color: #ffffff;
        border-color: $border_color;
        color: $text_color;
    }
    &.active-sort{
        background-color: #efeedb;
        &:active:focus,
            &.active,
            &:active,
            &:hover,
            &:focus{
            background-color: #efeedb;
        }
    }
    &_lg {
      display: inline-block;
      margin-left: 10px;
      padding-left: 15px;
      padding-right: 27px;
      border-radius: 3px;
      font-size: 14px;

      .glyphicon {
        margin-right: 10px;
        font-size: 12px;
      }
    }
    &_md {
      display: inline-block;
      margin-left: 4px;
      padding-left: 15px;
      padding-right: 20px;
      border-radius: 3px;
      font-size: 14px;

      .glyphicon {
        margin-right: 10px;
        font-size: 12px;
      }
    }
}
.btn{
    .icon{
        margin-right: 9px;
        &.user{
            margin-top: 4px;
        }
        &.catalog{
            margin-top: 7px;
        }
    }
}
.no-padding-left{
    padding-left: 0 !important;
}
.no-padding-right{
    padding-right: 0 !important;
}
@media (min-width: 992px) {
    .no-padding-md{
        padding-right: 0 !important;
        padding-left: 0 !important;
    }
    .no-padding-right-md{
        padding-right: 0 !important;
    }
    .no-padding-left-md{
        padding-left: 0 !important;
    }

}
@media (max-width: 991px) and (min-width: 768px) {
    .no-padding-sm{
        padding-right: 0 !important;
        padding-left: 0 !important;
    }
    .no-padding-right-sm{
        padding-right: 0 !important;
    }
    .no-padding-left-sm{
        padding-left: 0 !important;
    }

}
@media (max-width: 767px){
    .no-padding-right-xs{
        padding-right: 0 !important;
    }
    .no-padding-left-xs{
        padding-left: 0 !important;
    }
}
ul.site-menu-imtems{
    display: table;
    margin: 0;
    width: 100%;
    @media (max-width: 767px){
        display: block;
    }
    li{
        display: table-cell;
        @media (max-width: 767px){
            display: block;
        }
        .menu-item{
            position: relative;
            @media (max-width: 767px){
                border-top: 1px solid #42b5e3;
            }
            @media (min-width: 768px){
                &:after{
                    position: absolute;
                    content: '';
                    top: 0;
                    bottom: 0;
                    left: 0;
                    width: 1px;
                    background-color: #42b5e3;
                }
            }

        }
        &:first-child{
            .menu-item{
                padding-left: 5px;
                padding-right: 5px;
                &:after{
                    background-color: transparent;
                    @media (max-width: 767px){
                        border-top: none;
                    }
                }
            }
        }
    }
}
.main-container{
    @media (max-width: 991px){
        overflow: hidden;
    }
    @media (min-width: 768px){
        min-height: 500px;
    }
}
footer.footer{
    background: #e1ebee url(../bitrix/templates/s2018//images/bgh.png) 0 0 repeat;
    border-top: 1px solid  $border_color;
    padding: 44px 0 43px 0;
    line-height: 24px;
    font-size: 13px;
    color: $gray;
    position: relative;
    p{
        margin-bottom: 10px;
    }
    .row{
        @media (min-width: 992px) {
            >.col1{
                @include widthToPricentByPixelOn1200(285);
            }
            >.col2{
                @include widthToPricentByPixelOn1200(230);
            }
            >.col3{
                @include widthToPricentByPixelOn1200(401);
            }
            >.col4{
                @include widthToPricentByPixelOn1200(284);
            }
        }
    }
    a{
        color: $gray;
        &:hover{
            color: $blue;
            text-decoration: none;
        }
    }
    .logo-footer{
        margin-top: 6px;
    }
    .copyright{
        color: $light_gray;
        line-height: 1.2;
        margin-top: 50px;
    }
    .icon{
        &.phone{
            margin-top: 6px;
            margin-right: 9px;
        }
        &.email{
            margin-top: 6px;
            margin-right: 9px;
        }
        &.time{
            margin-top: 3px;
            margin-right: 9px;
        }

    }
    .social-links{
        margin-top: 20px;
        a{
            display: block;
            width: 33px;
            height: 34px;
            border-bottom: 1px solid #ffffff;
            background-color: #bac8cc;
            border-radius: 2px;
            margin-left: 7px;
            &:hover{
                background-color: #29ceff;
            }
            &:first-child{
                margin-left: 0;
            }
            .icon{
                &.tw{
                    margin-top: 11px;
                    margin-left: 10px;
                }
                &.fb{
                    margin-top: 11px;
                    margin-left: 13px;
                }
                &.vk{
                    margin-top: 12px;
                    margin-left: 13px;
                }
                &.ok{
                    margin-top: 11px;
                    margin-left: 12px;
                }
                &.g-plus{
                    margin-top: 11px;
                    margin-left: 10px;
                }
            }
        }
    }
    .btn-up {
        position: absolute;
        width: 64px;
        height: 64px;
        border-radius: 50%;
        background-color: #fff;
        padding: 5px;
        opacity: 0;
        top: -40px;
        right: 20px;
        z-index: 1000;
        transition: opacity .5s ease-in-out;

        &_content {
            width: 100%;
            height: 100%;
            padding-top: 15px;
            background: linear-gradient(#9ab0b8, #cad6db);
            text-transform: uppercase;
            border-radius: 50%;
            color: #fff;
            font-size: 10px;
            text-align: center;
            cursor: pointer;

            .icon.up {
                margin: 0 auto;
            }
        }

        &.fixed {
            position: fixed;
            bottom: 10px;
            top: auto;
        }
        &.visible {
            opacity: 1;
        }
    }
}

.main-page-row{

    @media (min-width: 992px) {
        margin-left: -17px;
        margin-right: -17px;
        >.col1{
            @include widthToPricentByPixelOn1200(269);
            padding-left: 17px;
            padding-right: 17px;
        }
        >.col2{
            @include widthToPricentByPixelOn1200(931);
            padding-left: 17px;
            padding-right: 17px;
            padding-top: 35px;
            position: relative;
            .sub-catalog{
                position: absolute;
                left: -17px;
                right: 17px;
                top: 0;
            }
        }
    }


}
@media (min-width: 992px) {
    .navbar-nav{
        float: none;
    }
}
.navbar-default{
    &.catalog-navbar{
        background: none;
        border-radius: 0;
        -webkit-box-shadow: none;
        box-shadow: none;
        border: 1px solid $border_color;
        .navbar-header{
            background-color: #27ae60;
            color: #ffffff;
            .navbar-brand{
                color: #ffffff;
                font-size: 14px;
                text-transform: uppercase;
                font-weight: bold;
                text-shadow: none;
                padding: 20px 15px;
                height: 60px;
            }
            .icon.catalog{
                margin-top: 25px;
                margin-left: 15px;
                @media (min-width: 992px) {
                    display: none;
                }
            }
        }
        .navbar-toggle{
            border-color: #ffffff;
            margin-top: 14px;
            margin-bottom: 13px;
            .icon-bar{
                background-color: #ffffff;
            }
            &:hover,
                &:focus{
                background-color: transparent;
            }

        }
        .navbar-nav{
            > li{
                float: none;
                border-top:  1px solid $border_color;
                &:first-child{
                    border-top: none;
                }
                a{
                    color: $text_color;
                    font-size: 14px;
                    padding-top: 14px;
                    padding-bottom: 14px;
                    padding-left: 20px;
                    .cnt{
                        color: $light_gray;
                    }
                }
                &:hover,
                    &.active{
                    > a {
                        background: none;
                        -webkit-box-shadow: none;
                        box-shadow: none;
                        background-color: $very_light_blue;
                    }
                }
                .dropdown-menu{
                    @media (min-width: 992px) {
                        display: none !important;
                    }
                }
            }
            > .open{
                > a{
                    background: none;
                    background-color: $very_light_blue;
                    -webkit-box-shadow: none;
                    box-shadow: none;
                }
            }
        }
        .navbar-collapse{
            @media (min-width: 768px){
                padding-left: 0;
                padding-right: 0;
            }
        }
    }
    .container-fluid{
        @media (min-width: 768px){
            padding-left: 0;
            padding-right: 0;
        }
    }
}
.special-img-wrap{
    display: block;
    position: relative;
    margin-bottom: 30px;
    &:hover{
        text-decoration: none;
    }
    .special{
        display: block;
        font-size: 14px;
        font-weight: bold;
        color: $text_color;
        border-left: 3px solid $green;
        background-color: $very_light_blue;
        line-height: 1.2;
        padding: 19px 15px 19px 25px;
        margin: 0;
        text-transform: uppercase;
        &:hover{
            background-color: $green;
            color: #ffffff;
        }
    }
    .price{
        display: block;
        position: absolute;
        bottom: 0;
        left: 0;
        font-size: 18px;
        line-height: 1.2;
        background-color: $blue;
        color: #ffffff;
        padding: 17px 32px 16px 32px;
        font-weight: bold;
    }
    .price-small{
        display: block;
        position: absolute;
        bottom: 0;
        left: 0;
        font-size: 13px;
        line-height: 1.2;
        font-weight: normal;
        background-color: $blue;
        color: #ffffff;
        padding: 13px 18px 12px 18px;
    }
}
.detail-in-link{
    display: block;
    font-size: 14px;
    line-height: 1.5;
    color: $text_color;
    text-decoration: none;
    a:hover &{
        color: $link_color;
        text-decoration: none;
    }
}
a.main-news-item{
    display: block;
    margin-bottom: 30px;
    text-decoration: none;
    .special-img-wrap{
        margin-bottom: 20px;
    }
}
h3,
.h3{
    font-weight: bold;
    font-size: 18px;
    margin-top: 0;
    margin-bottom: 18px;
}
.margin-bottom-43{
    margin-bottom: 43px;
}
p{
    margin-bottom: 24px;
}
@media (min-width: 992px) {
    .row.row-m-17{
        margin-left: -17px;
        margin-right: -17px;
        > div{
            padding-left: 17px;
            padding-right: 17px;
        }
    }

}
.h3.special,
h3.special,
.h2.special,
h2.special{
    position: relative;
    margin: 0 0 29px 0;
    font-size: 20px;
    line-height: 1.2;
    font-weight: bold;
    text-transform: uppercase;
    .title{
        position: relative;
        background-color: #ffffff;
        padding-right: 7px;
    }
    &:before{
        content: '';
        position: absolute;
        display: block;
        background-color: $border_color;
        top: 50%;
        left: 0;
        right: 0;
        height: 1px;
    }
}
h1.border-top,
.h1.border-top{
    font-size: 30px;
    color: $text_color;
    font-weight: bold;
    margin-top: 7px;
    line-height: 1.2;
    padding-top: 19px;
    margin-bottom: 19px;
    border-top: 1px solid $border_color;
}
h1.h2.special{
    margin-bottom: 25px;
}
.h3.special,
h3.special{
    font-size: 16px;
    .title{
        color: $text_color;
    }
    .icon{
        display: inline-block;
        top: 3px;
        position: relative;
        margin-left: 5px;
    }
}
section.reviews{
    .review-item{
        a{
            display: block;
            position: relative;
            .hover{
                display: none;
                content: '';
                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
                background-color: rgba(39, 174, 96, 0.5);
                .icon.big-zoom{
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    margin-top: -41px;
                    margin-left: -41px;
                }
            }
            &:hover{
                .hover{
                    display: block;
                }
            }
        }
        img{
            display: block;
            border: 1px solid $border_color;
        }
    }

}
.owl-carousel{
    margin-bottom: 39px;
    .owl-nav{
        position: absolute;
        top: -57px;
        right: 0;
        padding: 0;
        margin: 0;
        padding-left: 6px;
        background-color: #ffffff;
        .owl-next,
        .owl-prev{
            display: inline-block;
            background-color: $blue;
            width: 35px;
            height: 35px;
            border-radius: 0;
            margin: 0;
            padding: 13px 14px 13px 14px;
            &:hover{
                background-color: $light_blue;
            }
            &.disabled:hover{
                background-color: $blue;
            }
        }
        .owl-prev{
            margin-right: 1px;
        }
    }
}
.fancybox-is-open .fancybox-bg {
    opacity: .2;
}
.btn-fancybox {
    position: absolute;
    bottom: 0;
    width: 35px;
    height: 35px;
    border-radius: 0;
    margin: 0;
    padding: 13px 14px 13px 14px;
    background-color: $blue;
    z-index: 1000;
    &:hover {
        background-color: $light_blue;
    }
    &.button-next {
        right:0;
    }
    &.button-prev {
        right: 37px;
    }

    &.disabled {
        opacity: 0.5;
        cursor: default;
    }
}
section.audio-reviews{
    audio{
        width: 100%;
    }
}
section.main-about{
    margin-bottom: 19px;
    h2{
        margin-top: 0;
        font-weight: normal;
        font-size: 24px;
        color: $text_color;
    }
}
.audio-track-wrap{
    padding: 11px 22px 11px 23px;
    margin-bottom: 10px;
    background-color: $very_light_blue;
    line-height: 1;
    font-size: 14px;
    audio{
        display: none;
    }
    .play_audio{
        display: block;
        background-color:$text_color;
        padding: 10px 0 9px 0;
        width: 28px;
        margin-right: 11px;
        .icon{
            margin-left: auto;
            margin-right: auto;
        }
    }
    .prev_audio,
    .next_audio{
        display: block;
        padding: 10px 0 9px 0;
        width: 15px;
    }
    .audio-line-wrap-wrap{
        /*margin-left: 85px;*/
        margin-left: 43px;
    }
    .title{
        display: block;
        font-size: 14px;
        color: $text_color;
        line-height: 1;
        height: 14px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        margin-right: 40px;
    }
    .time{
        display: block;
        font-size: 14px;
        color: $gray;
        line-height: 1;
        width: 40px;
    }
    .audio-line-wrap{
        padding-top: 4px;
        padding-bottom: 4px;
        margin-top: 4px;
        cursor: pointer;
    }
    .audio-line{
        background-color: #cfdfe6;
        height: 2px;
        .progress-track{
            background-color: $green;
            height: 2px;
            width: 0;
        }
    }
    .title-wrap{
        height: 14px;
    }
}
.search_result_wrap{
    position: relative;
    .search_result{
        position: absolute;
        top: -1px;
        left: 0;
        right: 0;
        border: 1px solid #d1d1d1;
        // padding: 8px 14px 7px 14px;
        background-color: #ffffff;
        z-index: 1100;
        box-shadow:         0 4px 4px rgba(0,0,0,.12);
        -moz-box-shadow:    0 4px 4px rgba(0,0,0,.12);
        -webkit-box-shadow: 0 4px 4px rgba(0,0,0,.12);
        box-shadow:         0 4px 4px rgba(0,0,0,.12);
        &_row {
          display: table;
          width: 100%;
          &:not(:first-child) {
            border-top: 1px solid #d6d6d6;
          }
        }
        .item{
            display: table;
            width: 100%;
            padding: 18px 20px 15px 20px;
            &:not(:last-child) {
              border-bottom: 1px solid #d6d6d6;
            }
            .img-wrap{
                display: table-cell;
                vertical-align: middle;
                text-align: center;
                width: 42px;
                height: 42px;
                border: 1px solid #d1d1d1;
                img {
                  max-width: 100%;
                  object-fit: contain;
                  height: 42px;
                }
            }
            .product-name{
                max-width: 350px;
                padding: 0 0 0 10px;
                float: left;
                @media (max-width: 767px) {
                    float: none;
                }
            }
            .product-name a{
                font-size: 13px;
                line-height: 1;
                color: #444444;
                text-decoration: none;

                &.text-blue {
                  color: #0f8dbf;
                }
            }
            &:hover .product-name a{
                color: #83c001;
            }
            .product-total {
              width: 80px;
              margin-left: auto;
              text-align: right;
              @media (max-width: 767px) {
                  width: auto;
              }
            }
        }
        .product-category {
          background-color: $very_light_blue;
          display: table-cell;
          padding: 18px 10px 15px 22px;
          width: 110px;
          border-right: 1px solid #d1d1d1;
          word-break: break-all;
          overflow: hidden;
          @media (max-width: 767px) {
              width: 85px;
              padding-left: 10px;
          }
        }

        .product-all-result {
          color: $gray;
          padding-left: 20px;
          padding-top: 13px;
          padding-bottom: 13px;
        }
        .text-sm {
          color: $light_gray;
          font-size: 12px;
        }

    }
}
.cart-block .fast-cart {
    /*    display: none;
        position: absolute;
        right: 33px;
        top: 109px;
        width: 400px;
        background-color: #ffffff;
        border: 1px #d1d1d1 solid;
        -moz-box-shadow: 0 4px 20px rgba(0, 0, 0, 0.12);
