        z-index: 9997;*/
    .fast-cart-wrap{
        position: relative;
    }
    .fast-cart-body {
        /*height: 400px;*/
    }
    .fast-cart-footer {
        border-top: 1px #ffffff solid;
        padding-top: 19px;
        @media (min-width: 768px) {
            padding-left: 18px;
            padding-right: 19px;
        }

        .inner {
            background-color: #f1f1f1;
            padding: 4px 0 15px 0;
        }
    }
    .fast-cart-body .item {
        padding: 21px 0;
        position: relative;
        border-bottom: 1px solid #ffffff;
        &.first {
            border-top: 1px solid #ffffff;
        }
        .item-img {
            width: 77px;
            float: left;
        }
        .img-wrap-wrap {
            display: table;
            width: 77px;
            height: 77px;
            border: 1px #d1d1d1 solid;
            background-color: #ffffff;
        }
        .img-wrap {
            display: table-cell;
            text-align: center;
            vertical-align: middle;
        }
        .item-name {
            width: 261px;
            padding: 0 0px 0 16px;
            float: left;
            @media (max-width: 767px) {
                width: auto;
                float: none;
                margin-left: 77px;
                &:after{
                    content: '';
                    display: table;
                    width: 100%;
                    clear: both;
                }
            }
            > .name {
                padding-bottom: 8px;
            }
            .name a {
                color: $text_color;
                text-decoration: none;
                font-size: 14px;
                line-height: 1.2;
                &:hover {
                    color: #0f8dbf;
                }
            }
        }
        .property{
            color: $light_gray;
            font-size: 12px;
        }
        .item-price {
            width: 87px;
            padding: 0 0 0 0px;
            min-height: 72px;
            position: relative;
            text-align: right;
            float: left;
            @media (max-width: 767px) {
                float: none;
                width: auto;
                margin-left: 92px;
                text-align: left;
                padding-top: 15px;

            }
            .price {
                line-height: 12px;
                font-size: 14px;
                margin-bottom: 26px;
                @media (max-width: 767px) {
                    width: 50%;
                    float: left;
                }
            }
            .del{
                @media (max-width: 767px) {
                    width: 50%;
                    float: left;
                    text-align: right;
                }
            }
        }
    }
    .total{
        color: $light_gray;
        font-size: 14px;
        line-height: 21px;
        padding-top: 16px;
        text-align: center;
        @media (min-width: 768px) {
            text-align: left;
        }
        > div{
            padding-bottom: 16px;
        }
        .price{
            font-weight: bold;
            color: $text_color;

            font-size: 16px;
            line-height: 21px;
            @media (min-width: 768px) {
                text-align: right;
            }
        }
    }
    .del {
        color: #0f8dbf;
        font-size: 12px;
    }
}
