    }
    .modal-body{
        position: relative;
        background-color: $very_light_blue;
        padding: 20px 15px 3px 15px;
        p{
            margin-bottom: 14px;
        }
    }
    .close{
        position: absolute;
        top: -9px;
        right: -9px;
        width: 29px;
        height: 29px;
        padding: 5px 0 0 8px;
        background-color: #ffffff;
        opacity: 1;
    }
    .modal-user-sm{
        .modal-body{
            padding: 22px 25px 37px 25px;
        }
    }
    .modal-user-auth-sm{
        .modal-body{
            padding: 23px 24px 37px 24px;
        }
    }
    .modal-fast-cart-sm{
        .modal-body{
            padding: 21px 21px 26px 24px;
        }
    }
    @media (min-width: 768px){
        .modal-sm{
            width: 318px;
            &.modal-user-sm{
                width: 423px;
            }
            &.modal-user-auth-sm{
                width: 352px;
            }
            &.modal-fast-cart-sm{
                width: 488px;
            }
        }
    }

}
