.select-checkboxed{
    margin-bottom: 12px;
    position: relative;
    .title{
        display: block;
        position: relative;
        padding: 10px 24px 10px 17px;
        border: 1px solid $border_color;
        border-radius: 4px;
        font-size: 14px;
        line-height: 1.2;
        color: $text_color;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        cursor: pointer;
        .i-down{
            position: absolute;
            top: 17px;
            right: 14px;
            border: 4px solid transparent;
            border-top: 4px solid #000000;

        }
    }
    .list{
        position: absolute;
        display: none;
        background-color: #ffffff;
        left: 0px;
        right: 0px;
        border: 1px solid $border_color;
        z-index: 940;
        margin-top: -1px;
        padding: 7px 11px 10px 17px;
        font-size: 14px;
        color: $text_color;
        -webkit-box-shadow: 3px 4px 5px 0px rgba(0, 0, 2, 0.1);
        -moz-box-shadow:    3px 4px 5px 0px rgba(0, 0, 2, 0.1);
        box-shadow:         3px 4px 5px 0px rgba(0, 0, 2, 0.1);
        .inner{
            max-height: 181px;
            overflow-y: auto;
            width: 100% !important;
            .checkbox{
                width: 100%;
                margin-top: 1px;
                outline: none;
                margin-bottom: 1px;
                input{
                    outline: none;
                }
                label{
                    padding-left: 8px;
                    outline: none;
                    .count{
                        color: $light_gray;
                    }
                    &:before{
                        width: 18px;
                        height: 18px;
                        outline: none;
                    }
                    &:after{
                        font-size: 14px;
                        color: #333333;
                        padding-top: 0;
                        padding-left: 3px;
                        outline: none;
                    }
                }
            }
        }
    }
    .select-checkboxed-wrap &{
        margin-right: 40px;
    }
