        padding: 5px 0px 0px 0px;
        margin-top: -5px;
        background-color: #fff;
        border-top: 3px solid #c5d4db;
        border-radius: 3px;
        width: 245px;

         @media (max-width: 768px) {
             width: 230px;
         }
        .countdown-text{
            color: inherit;
            font-style: normal;
            font-size: 12px;
            text-align: center;
            font-family: inherit;
        }
        .is-countdown{
            text-align: center;
            margin: 0;
            height: auto;
            border-radius: 0;
            -webkit-border-radius: 0px;
            -moz-border-radius: 0px;
            border: none;
            background-color: transparent;
            .num{
                background: none;
                width: auto;
                height: 1em;
                line-height: 1;
                font-family: 'Roboto', sans-serif;
                font-weight: 400;
            }
        }
        .countdown-show7 .countdown-section,
        .countdown-show6 .countdown-section,
        .countdown-show5 .countdown-section,
        .countdown-show4 .countdown-section,
        .countdown-show3 .countdown-section,
        .countdown-show2 .countdown-section,
        .countdown-show1 .countdown-section {
            width: auto;
        }
        .countdown-amount {
            position: relative;
            display: inline-block;
            font-size: 24px;
            line-height: 1.1;
            font-family: 'Roboto', sans-serif;
            font-weight: 400;
            float: none;
            padding-right: 20px;
            width: 61px;
            color: $text-color;
            &:after{
                background: none;
                content: ':';
                font-size: 24px;
                width: 29px;
                text-align: center;
                height: auto;
                position: absolute;
                top: 0;
                left: auto;
                right: 0;
                bottom: 0;
                display: block;
                float: none;
            }
        }
        .countdown-section {
            display: inline-block;
            float: none;
        }
        .countdown-period {
            color: inherit;
            font-size: 12px;
            padding-right: 20px;
            padding-bottom: 5px;
            color: $gray;
        }
    }
    .countdown-row .countdown-section:last-child .countdown-amount{
        color: #ff0000;
        padding-right: 0px;
        width: 32px;
    }
    .countdown-row .countdown-section:last-child .countdown-period{
        padding-right: 0px;
    }
}

