    .checkbox {
        &_wr {
            display: block;
            cursor: pointer;

            input {
                position: absolute;
                opacity: 0;
            }
            .checkmark {
                position: absolute;
                display: block;
                position: absolute;
                left: -15px;
                top: 40px;
                width: 17px;
                height: 17px;
                border: 2px solid #d4e0e6;
                background-color: #fff;

                &_box {
                    border-radius: 1px;
                }
                &_round {
                    border-radius: 50%;
                }
            }
            & input:checked ~ .checkmark_box:after {
                content: '';
                position: absolute;
                display: block;
                left: 5px;
                top: 2px;
                width: 5px;
                height: 9px;
                border: solid $text-color;
                border-width: 0 2px 2px 0;
                transform: rotate(45deg);
            }
            & input:checked ~ .checkmark_round:after {
                content: '';
                position: absolute;
                display: block;
                left: 2px;
                top: 2px;
                width: 9px;
                height: 9px;
                background-color: $text-color;
                border-radius: 50%;
            }
        }
