            padding-bottom: 5px;
            text-align: left;

            &:after {
                position: absolute;
                content: '';
                top:48%;
                right: 20px;
                border: 4px solid transparent;
                border-top: 4px solid #fff;
            }

            &:hover {
                background-color: #768991;
                transition: all 0.5s ease-in-out;
            }
        }
    }
    &_content {
        margin-left: -1px;
        margin-right: 2px;
        padding-bottom: 20px;
    }
