        }
        .owl-carousel .owl-item img  {
            width: auto;
            max-width: 100%;
        }
        .owl-carousel .owl-nav.disabled {
            display: block;
        }

        & .owl-carousel .owl-nav {
            position: static;
        }
        & .owl-nav .owl-next,
        & .owl-nav .owl-prev,
        & .owl-nav  .owl-prev.disabled,
        & .owl-nav  .owl-next.disabled {
            background-color: transparent;
            &:hover {
                background-color: transparent;
            }
        }
        &  .owl-nav .owl-prev,
        & .owl-nav  .owl-prev.disabled, {
            position: absolute;
            top: calc(50% - 23px);
            left: -40px;
            width: 15px;
            padding: 0;
        }
