      display: inline-block;
  }
  .panel-heading {
      height: 50px;
      line-height: 50px;
      background-color: #eaf3f7;
      position: relative;
      cursor: pointer;
      padding: 0;

      a {
          width: 100%;
          display: inline-block;
          padding-left: 65px;
          color: $text-color;
          text-decoration: none;
      }
  }

  .cross {
      position: absolute;
      top: 0;
      left: 0;
      width: 50px;
      height: 50px;
      background-color: #c3d2d9;
      pointer-events: none;

      &:after {
          content: '';
          position: absolute;
          top: 0;
          bottom: 0;
          left: 0;
          right: 0;
          margin: auto;
          width: 12px;
          height: 4px;
          background-color: #fff;
      }

      &.closed:before {
          content: '';
          position: absolute;
          top: 0;
          bottom: 0;
          left: 0;
          right: 0;
          margin: auto;
          width: 4px;
          height: 12px;
          background-color: #fff;
      }
  }
  .panel-body {
    padding-left: 70px;
    padding-right: 70px;
  }
  &_table {
    width: 100%;
    border: 1px solid #d4e0e6;
    color: $text_color;
    tr {
      height: 55px;
    }
    td {
      padding-left: 20px;
      padding-right: 10px;
    }
    th {
      background-color: #efeeda;
      text-align: center;
    }
    th:not(:last-child),
          td:not(:last-child) {
          border-right: 1px solid #d4e0e6;
      }
      tr:not(:last-child) {
          border-bottom: 1px solid #d4e0e6;
      }
    .right-col {
      width: 12.84%;
    }
    .left-col {
      width: 87.16%;
    }
  }
}

//articles
.product-list {
    border: 1px solid #d4e0e6;
    border-radius: 0;
    li {
      padding-top: 14px;
      padding-right: 14px;
      padding-bottom: 13px;
      padding-left: 14px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;

      &:hover {
        background-color: $very_light_blue;
      }
      @media (max-width:768px) {
       padding: 10px;
      }

      &:not(:last-child) {
        border-bottom: 1px solid $border_color;
      }

      &.active {
        background-color: $very_light_blue;
      }
    }

  &_item {
    position: relative;
    padding-left: 20px;
    color: inherit;
    display: inline-block;
    width: 100%;

    &:before {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      margin: auto;
      width: 0;
      height: 0;
      border-top: 4px solid transparent;
      border-bottom: 4px solid transparent;
      border-left: 4px solid #000;
    }
    &:hover,
    &:focus {
      text-decoration: none;

      span {
        color: inherit;
      }
    }
    span {
      color: $light_gray;
    }
  }
  &_nav {
    border: none;
  }
}

