  &_header {
    font-size: 30px;
    font-weight: 600;
    @media (max-width:768px) {
       font-size: 25px;
    }
  }
  &_sub-header {
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 25px;
    padding-top: 10px;
  }
  &_list {
    padding-left: 25px;
  }
  &_list-item {
    position: relative;
    padding-left: 20px;
    margin-bottom: 20px;

    &:before {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      margin: auto;
      width: 3px;
      height: 3px;
      border-radius: 50%;
      background-color: $green;
    }
  }
  &_nav {
    max-width: 100%;
    width: auto !important;
    max-height: calc(100vh - 400px);
    // max-height: calc(100vh - 400px);
    padding-right: 0 !important;
    border: 1px solid $border-color;
  }
  &_nav > li > a:hover,
  &_nav > li > a:focus {
    text-decoration: none;
    background-color: inherit;
}
  &_nav > li > a {
      position: relative;
      display: block;
      padding: 0;
      padding-left: 20px;
      overflow: hidden;
      text-overflow: ellipsis;
  }
}
.vertical-track3 {
  width: 3px;
  background-color: transparent;
}

.callme-modal {
  position: relative;
  color: $gray;

  .modal {
    position: absolute;
    right: -17px;
    top: 15px;
    bottom: auto;
    left: auto;

    @media (max-width: 767px){
        top: 100%;
        right: 0;
        left: 0;
        margin: auto;
    }
  }

  .modal-dialog {
    width: 340px;
    @media (max-width: 767px){
      width: auto;
    }
  }

  .modal-content:before {
    position: absolute;
    content: '';
    top: -10px;
    right: 80px;
    border: 6px solid transparent;
    border-bottom: 6px solid #ffffff;

    @media (max-width: 767px){
      right: calc(50% - 3px);
    }
  }

  .modal.my_modal .modal-body {
    position: relative;
    background-color: #eaf3f7;
    padding: 28px 20px 30px 25px;
  }

  .modal-header {
    padding: 0;
    border-bottom: none;
    position: relative;
    z-index: 1;
  }

  .modal.my_modal .close {
    top: 0;
    right: -5px;
    width: 25px;
    height: 25px;
    padding: 0 0 5px 7px;
  }

  .btn {
    width: 100%;
  }

  .form-group {
    position: relative;

    .icon {
      position: absolute;
      left: 15px;
      top: 0;
      bottom: 0;
      margin: auto;
    }
  }

  .form-control {
    padding-left: 40px;
  }

  input::-webkit-input-placeholder {
    color: $light_gray;
  }
}
.buy-modal {
  .modal-header {
    padding-top: 20px;
    background-color: #eaf3f7;
    color: $text-color;
  }
  .modal-title {
    font-weight: 700;
  }
  .modal.my_modal .modal-body {
    padding-bottom: 20px;
  }
  .modal {

    @media (max-width: 767px){
        top: 100%;
        right: -47px;
        left: -30px;
    }
  }
}
